
.hsy-dialog {
  display: none;
  font: 10px "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", SimSun, sans-serif;
  position: absolute;
  top: 0;
  left: 0;
}
.hsy-dialog .mask {
  width: 100%;
  height: 100%;
}
.hsy-dialog .main {
  position: absolute;
  margin: 0 auto;
  top: 50%;
}
.hsy-dialog .main>div {
  margin: 0 auto;
}
.hsy-dialog .main .title {
  position: relative;
  color: #0E3569;
  height: 40px;
  line-height: 40px;
  overflow: hidden;
}
.hsy-dialog .main .title .content>* {
  width: 80%;
  min-width: 85px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.hsy-dialog .main .title .content {
  font-size: 1.8em;
  min-height: 20px;
  vertical-align: middle;
}
.hsy-dialog .main .title .btnClose {
  width: 20px;
  height: 20px;
  float: right;
  cursor: pointer;
  position: absolute;
  right: 0;
  top: 50%;
  -webkit-transform: translate(0, -50%);
          transform: translate(0, -50%);
}
.hsy-dialog .main .body {
  max-width: 46vw;
  color: #6A7288;
  font-size: 1.2em;
  border-top: 1px solid #eee;
  margin-top: 5px;
  padding-top: 15px;
}
.hsy-dialog.animated,
.hsy-dialog .animated {
  -webkit-animation-duration: 0.35s;
          animation-duration: 0.35s;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
}
@-webkit-keyframes fadeInDown {
from {
    opacity: 0;
    -webkit-transform: translate3d(0, -30%, 0);
    transform: translate3d(0, -30%, 0);
}
to {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
}
}
@keyframes fadeInDown {
from {
    opacity: 0;
    -webkit-transform: translate3d(0, -30%, 0);
    transform: translate3d(0, -30%, 0);
}
to {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
}
}
.hsy-dialog.fadeInDown,
.hsy-dialog .fadeInDown {
  -webkit-animation-name: fadeInDown;
          animation-name: fadeInDown;
}
@-webkit-keyframes fadeOut {
from {
    opacity: 1;
}
to {
    opacity: 0;
}
}
@keyframes fadeOut {
from {
    opacity: 1;
}
to {
    opacity: 0;
}
}
.hsy-dialog.fadeOut,
.hsy-dialog .fadeOut {
  -webkit-animation-name: fadeOut;
          animation-name: fadeOut;
}
.spinner {
  margin: 0 auto;
  width: 50px;
  height: 50px;
  position: relative;
}
.container1 > div, .container2 > div, .container3 > div {
  width: 15px;
  height: 15px;
  background-color: #333;

  border-radius: 100%;
  position: absolute;
  -webkit-animation: bouncedelay 1.2s infinite ease-in-out;
  animation: bouncedelay 1.2s infinite ease-in-out;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}
.spinner .spinner-container {
  position: absolute;
  width: 100%;
  height: 100%;
}
.container2 {
  -webkit-transform: rotateZ(45deg);
  transform: rotateZ(45deg);
}
.container3 {
  -webkit-transform: rotateZ(90deg);
  transform: rotateZ(90deg);
}
.circle1 { top: 0; left: 0;
}
.circle2 { top: 0; right: 0;
}
.circle3 { right: 0; bottom: 0;
}
.circle4 { left: 0; bottom: 0;
}
.container2 .circle1 {
  -webkit-animation-delay: -1.1s;
  animation-delay: -1.1s;
}
.container3 .circle1 {
  -webkit-animation-delay: -1.0s;
  animation-delay: -1.0s;
}
.container1 .circle2 {
  -webkit-animation-delay: -0.9s;
  animation-delay: -0.9s;
}
.container2 .circle2 {
  -webkit-animation-delay: -0.8s;
  animation-delay: -0.8s;
}
.container3 .circle2 {
  -webkit-animation-delay: -0.7s;
  animation-delay: -0.7s;
}
.container1 .circle3 {
  -webkit-animation-delay: -0.6s;
  animation-delay: -0.6s;
}
.container2 .circle3 {
  -webkit-animation-delay: -0.5s;
  animation-delay: -0.5s;
}
.container3 .circle3 {
  -webkit-animation-delay: -0.4s;
  animation-delay: -0.4s;
}
.container1 .circle4 {
  -webkit-animation-delay: -0.3s;
  animation-delay: -0.3s;
}
.container2 .circle4 {
  -webkit-animation-delay: -0.2s;
  animation-delay: -0.2s;
}
.container3 .circle4 {
  -webkit-animation-delay: -0.1s;
  animation-delay: -0.1s;
}
@-webkit-keyframes bouncedelay {
0%, 80%, 100% { -webkit-transform: scale(0.0)
}
40% { -webkit-transform: scale(1.0)
}
}
@keyframes bouncedelay {
0%, 80%, 100% {
    transform: scale(0.0);
    -webkit-transform: scale(0.0);
}
40% {
    transform: scale(1.0);
    -webkit-transform: scale(1.0);
}
}



